import React from 'react'
import './VideoRow.css';

function VideoRow({views, description, timestamp, channelName, title , image, subs}) {
    return (
        <div>
           <div className="videoRow">
               <img src={image}/>
               <div className="videoRow__text">
               <h3>{title}</h3>
               <p className="videoRow__info">{channelName} .<span className="videoRow__subs"> <span className="videoRow__subsNumber">{subs} </span> Subscribers </span>.{views} views . {timestamp}</p>
                <p className="videoRow__description">{description}</p>
               </div>
           </div> 
        </div>
    )
}

export default VideoRow;
